import { Component, OnInit } from '@angular/core'; import { Router, ActivatedRoute } from '@angular/router'; import { Subscription } from 'rxjs/Subscription';  import { Acao_preventiva_formulario } from '../../../modelo/Acao_preventiva_formulario'; import { ObjetoVirtual } from '../../../modelo/ObjetoVirtual'; import { PesquisaService } from '../../../core/services/pesquisa/pesquisa.service';  @Component({   moduleId: module.id,   selector: 'acao_preventiva_formulario-dados-details',   templateUrl: 'acao_preventiva_formulario_dadosDetails.component.html' }) export class Acao_preventiva_formularioDadosDetailsComponent implements OnInit {    acao_preventiva_formulario: Acao_preventiva_formulario;    private sub: Subscription;    private errorMessage: string;    constructor(private route: ActivatedRoute, private pesquisaService: PesquisaService) { }    ngOnInit() {       this.sub = this.route.parent.params.subscribe(params => {         let o : ObjetoVirtual = new ObjetoVirtual();         o.classeJava = "br.com.primum.modelo.acao_preventiva_formulario.Acao_preventiva_formulario";                 let id = +params['id'];         this.acao_preventiva_formulario = new Acao_preventiva_formulario();         this.acao_preventiva_formulario.cd_acao_corretiva_formulario = id;         o.objetoJson = this.acao_preventiva_formulario;          this.pesquisaService.efetuarPesquisa(o)             .subscribe(             result => this.onEfetuarPesquisa(result),             error =>  this.errorMessage = <any>error);       });   }    onEfetuarPesquisa(result : any) {     if (!result) {       this.errorMessage = 'Houve um erro ao pesquisa a acao_preventiva_formulario. '; }      this.acao_preventiva_formulario = result;   }    ngOnDestroy() {     this.sub.unsubscribe();   } }


